<template>
  <div class="loginWrapper" id="loginBackground" > 
    <div class="formWrapper">
      <el-card class="box-card" >
      <h1 class="loginTitle" style="text-align: center;">用户登录</h1>
      <el-form  :model="ruleForm"
        :rules="rules"
        ref="ruleForm"
        label-width="100px"
        class="ruleForm"
        hide-required-asterisk >
        <el-form-item  prop="telephone">
          <el-input
            prefix-icon="el-icon-mobile-phone"
            v-model="ruleForm.telephone"
            placeholder="手机号"></el-input>
        </el-form-item>
        <el-form-item prop="password">
          <el-input
            prefix-icon="el-icon-lock"
            v-model="ruleForm.password"
            placeholder="密码"
            show-password></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" 
          class="loginButton"
          @click="submitForm('ruleForm')" >登录</el-button>
        </el-form-item>
      <el-form-item>
        <div>
          <el-link type="primary"  class="resetClass" @click="resetPage" :underline="false" >忘记密码？</el-link> |
          <el-link type="primary" @click="registerPage" :underline="false" > 注册新账户</el-link>
        </div>
      </el-form-item>
      </el-form>
    </el-card>
    </div>
  </div>
</template>


<script>
import { login,} from "@/request/user.js"; //    引入登录接口
import Cookies from "js-cookie";

export default {
  name: "Login",
  data() {
    return {
      ruleForm: {
        telephone: "",
        password: "",
      },
      rules: {
        telephone: [
          { required: true, message: "请输入手机号", trigger: "blur" },
          {min: 11, max: 11,message: "手机号格式不正确！",trigger: "blur" },
        ],
        password: [
          { required: true, message: "请输入密码", trigger: "blur" },
          { min: 5, max: 20, message: "长度在 5 到 20 个字符",trigger: "blur"},
        ],
      },
    };
  },
  created() {
    if (this.$store.getters.isLogin) {
      // 用户若已登录，自动跳转到首页
      this.$notify({
          title: '成功',
          message: '您已登录！已跳转到首页',
          type: 'success'
      })
      this.$router.replace({ name: 'Home' })
    }
  },
  methods: {
    resetPage(){
      this.$router.replace({name:'ResetPwd'});
    },
    registerPage(){
      this.$router.replace({name:'Register'});
    },
    //  登录按钮-点击事件
    submitForm(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          // 各项校验通过-调用登录接口
          login(this.ruleForm, true).then((res) => {
            console.log(res);
            if (res.success) {
              Cookies.set("token", res.data.token); //    在cookies中添加token
              this.$message.success("登录成功！");
              this.$refs[formName].resetFields(); //    清空表单项
              this.$router.replace({ name: "Home",query: { fileType: 0, filePath: "/" },}); // 跳转到首页
            } else {
              this.$message.error("手机号或密码错误！");
            }
          });
        } else {
          this.$message.error("请完善信息！");
          return false;
        }
      });
    },
  },
};
</script>

<style  lang="stylus" scoped>

.loginWrapper {
  background-image: url("~@/assets/xt.jpg");
  width:calc(100%);
  height: 100%;
  position: fixed;
  background-size: 100% 100%;
}
.box-card{
  border-radius: 20px;
  width: 400px;
  margin-top: 80px;
  height: 450px;
}
.formWrapper {
    width: 300px;
    margin-top: 50px;
    margin-left: 30px;
    margin-right: 40px;
  }
    .loginTitle {
      margin-bottom: 10px;
      font-weight: 300;
      font-size: 30px;
      color: #000;
    }
.loginButton{
  width :100%;
}
.resetClass{
  margin-left:80px;
}
.ruleForm {
  width: 400px;
  margin-top: 50px;
  margin-left: -60px;
  
}
  
</style>